<template>
  <div id="app">
    <div id="nav">
      <img class="logo" :src="require('@/assets/images/Logo.png')" alt="Logo" />
      <div class="buttons" >
        <div v-for="button in navButtons" :key="button.path" :class="'button' + (button.path === activityPath ? ' activity' : '')" @click="linkTo(button.path)">
          {{ button.name }}
        </div>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
export default {
  data () {
    return {
      navButtons: [
        { name: 'iOS', path: '/ios' },
        { name: 'Vue', path: '/vue' },
        { name: 'JS', path: '/js' },
        { name: '小程序', path: '/mp' }
      ]
    }
  },
  computed: {
    activityPath () {
      let path = this.$route.path
      if (path.lastIndexOf('/') === path.length - 1) path = path.slice(0, path.length - 1)
      return path
    }
  },
  methods: {
    linkTo (path) {
      this.$router.push({ path })
    }
  }
}
</script>

