import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home/Home.vue'
import iOSHome from '../views/iOS/home/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: home
  },
  {
    path: '/iOS',
    name: 'iOSHome',
    component: iOSHome
  }
]

const router = new VueRouter({
  routes
})

export default router
